module.exports = ".lineThrough {\n    position: relative;\n    display: block;\n    cursor: pointer;\n}\n\n.lineThrough:before, .lineThrough:after {\n    content: '';\n    position: absolute;\n    width: 0%;\n    height: 2px;\n    top: 50%;\n    margin-top: -0.5px;\n    background: #fff;\n}\n\n.lineThrough:before {\n    left: -2.5px;\n}\n.lineThrough:after {\n    right: 2.5px;\n    background: #5778F3;\n    transition: width 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);\n}\n\n.lineThrough:hover:before {\n    background: #5778F3;\n    width: 100%;\n    transition: width 0.5s cubic-bezier(0.22, 0.61, 0.36, 1);\n}\n\n.lineThrough:hover:after {\n    background: transparent;\n    width: 100%;\n    transition: 0s;\n}\n\n\n"