module.exports = ".textAnimationLeft {\n    height: 40px;\n    width: 120px;\n    text-align: center;\n    overflow: hidden;\n    background: #474E51;\n    position: relative;\n    line-height: 40px;\n    color: #fff;\n}\n\n.textAnimationLeft::before {\n    content: '';\n    position: absolute;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    height: 100%;\n    transform: translateX(-100%);\n    background: #5778F3;\n    transition: transform .25s ease-in;\n}\n\n.textAnimationLeft:hover::before {\n    transform: translateX(0);\n}\n\n.textAnimationLeft span {\n    position: relative;\n    z-index: 1;\n    display: block;\n}\n\n.textAnimationLeft:hover span {\n    animation: animButtonSpan 0.4s;\n}\n\n@keyframes animButtonSpan {\n    0% {\n        transform: translateX(0);\n        opacity: 1;\n    }\n\n    35% {\n        transform: translateX(20px);\n        opacity: 0;\n    }\n\n    50.001% {\n        transform: translateX(-20px);\n    }\n\n    60% {\n        transform: translateX(0px);\n    }\n\n}\n\n\n\n\n"