module.exports = ".radiusButton {\n    display: inline-block;\n    height: 40px;\n    line-height: 40px;\n    padding: 0 14px;\n    box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);\n    background: #474E51;\n    transition: all .15s ease;\n    position: relative;\n    overflow: hidden;\n    color: #fff;\n}\n\n.radiusButton::after {\n    content: \"\";\n    width: 4px;\n    height: 4px;\n    opacity: 0;\n    position: absolute;\n    border-radius: 50%;\n    transition: all 0.5s;\n    z-index: 1;\n    background: #5778F3;\n    top: calc(50% - 2px);\n    left: calc(50% - 2px);\n}\n\n.radiusButton:hover::after {\n    opacity: 1;\n    transform: scale(100);\n}\n\n.radiusButton span {\n    position: relative;\n    z-index: 2;\n}\n"