module.exports = ".iconRadius {\n    color: #474E51;\n    padding: 15px;\n    position: relative;\n}\n\n.iconRadius::before {\n    content: \"\";\n    position: absolute;\n    top: -1px;\n    bottom: -1px;\n    right: -1px;\n    left: -1px;\n    background-color: #5878F3;\n    z-index: -1;\n    border-radius: 50%;\n    transform: scale(0);\n    transition: transform .3s cubic-bezier(.215,.61,.355,1);\n}\n\n.iconRadius::after {\n    content: \"\";\n    position: absolute;\n    top: -1px;\n    bottom: -1px;\n    right: -1px;\n    left: -1px;\n    border: 2px solid #5878F3;\n    border-radius: 50%;\n    transform: scale(1);\n    opacity: 1;\n    transition: opacity .3s cubic-bezier(.215,.61,.355,1),transform .3s cubic-bezier(.215,.61,.355,1);\n}\n\n.iconRadius:hover::before {\n    transform: scale(1);\n}\n\n.iconRadius:hover::after {\n    transform: scale(1.2);\n    opacity: 0;\n}\n"